import './App.css';
import {useDispatch, useSelector} from 'react-redux';
import {init} from "./reducers/authReducer";
import axios from "axios";
import React, {useEffect} from "react";
import AdminLayout from "./components/layouts/SbAdmin/AdminLayout";
import {get} from 'lodash';
import {ToastContainer} from 'react-toastify';

import "react-datepicker/dist/react-datepicker.css";
import 'react-toastify/dist/ReactToastify.css';
import {Helmet} from "react-helmet";

axios.defaults.withCredentials = true

function App() {

    const {user, site} = useSelector(state => state.init);
    const dispatch = useDispatch();

    useEffect(() => {
        dispatch(init());
    }, [dispatch]);


    if (!user || !site) {
        return "loading...";
    }

    const domain = get(site, 'full_domain', '');

    if (domain) {
        axios.defaults.baseURL = domain;
    }

    return (
        <div className="App">

            <Helmet>
                <title>{site.company_name} Portal</title>
                <link rel="icon" href={site.logo || "https://trenchdevs.org/favicon.png"}/>
                <link rel="apple-touch-icon" href={site.logo || "https://trenchdevs.org/favicon.png"}/>
            </Helmet>
            <AdminLayout>
                Hello {user.first_name}
            </AdminLayout>
            <ToastContainer/>
        </div>
    );
}

export default App;
