import React from 'react';
import axios from "axios";
import MaterialTable from "material-table";

/**
 * Laravel Compatible Table
 * @param endpoint
 * @param columns
 * @param title
 * @param options
 * @param props
 * @returns {JSX.Element}
 * @constructor
 */
export default function AxiosTable({
                                       endpoint,
                                       columns,
                                       title,
                                       options = {
                                           search: false,
                                           filtering: true,
                                           sorting: false,
                                       },
                                       actions = [],
                                   }) {


    return (

        <MaterialTable
            options={options}
            columns={columns}
            data={query =>
                new Promise((resolve, reject) => {
                    axios.post(endpoint, query).then(response => {
                        resolve(response.data);
                    })
                })
            }
            title={title}
            actions={actions}
        />

    );

}