import {
    createSlice,
    createAsyncThunk
} from '@reduxjs/toolkit'
import axios from "axios";
import Endpoints from "../config/Endpoints";

export const logoutUser = createAsyncThunk(
    'auth/logout',
    () => { // arg, thunkAPI
        return axios.post(Endpoints.LOGOUT)
            .then(response => response.data);
    }
);

export const init = createAsyncThunk(
    'auth/init',
    () => { // arg, thunkAPI
        return axios.get(Endpoints.INIT, {withCredentials: true})
            .then(response => response.data);
    }
);

export const initReducer = createSlice({
    name: 'init',
    initialState: {
        user: {},
        site: {},
        // server: {},
    },
    reducers: {
        login: (state, payloadAction) => {
            const {payload} = payloadAction;
            return {
                /**
                 * Payload includes the user object
                 */
                ...payload,
            };
        },
        // clearUserData: () => ({}),
    },
    extraReducers: {
        /**
         * Thunk reducers
         */
        [init.pending]: (state, action) => {
            return {};
        },
        [init.rejected]: (state, action) => {
            return {};

        },
        [init.fulfilled]: (state, action) => {
            return {
                ...action.payload
            };
        },

        // ---------------- LOGOUT ----------------
        [logoutUser.pending]: (state, action) => {
            return {};
        },
        [logoutUser.rejected]: (state, action) => {
            return {};

        },
        [logoutUser.fulfilled]: (state, action) => {
            return {};
        },
        // ---------------- LOGOUT ----------------
    }
})

export const {login, clearUserData} = initReducer.actions

export default initReducer.reducer;