import React, {useState, useEffect} from 'react';
import {Redirect, useParams} from "react-router";
import StandardContainer from "../../../components/containers/StandardContainer";
import {Form, Col, Row} from "react-bootstrap";
import {get} from 'lodash';
import Endpoints from "../../../config/Endpoints";
import axios from 'axios';
import CustomDatePicker from "../../../components/dates/CustomDatePicker";
import {toast} from "react-toastify";
import CustomButton from "../../../components/buttons/CustomButton";

export default function EventUpsert() {

    const {id} = useParams();
    const updateMode = !!+id;

    const [redirect, setRedirect] = useState(null);

    const [formFields, setFormFields] = useState({
        name: '',
        location: '',
        description: '',
        from_date: '',
        to_date: null,
    });

    function onSimpleFormChange(e) {
        setFormFields({
            ...formFields,
            [e.target.name]: e.target.value,
        })
    }


    useEffect(() => {

        if (updateMode) {
            axios.get(`${Endpoints.GET_EVENT}/${id}`)
                .then(response => {

                    const {data, status, message} = get(response, 'data', {});

                    if (status !== 'success') {
                        toast.error(message);
                        setRedirect('/alph/events/');
                    }
                    setFormFields(data);

                })
                .catch(() => toast.error("There was a problem fetching event"));
        }

        return () => console.log('UNMOUNTED');

        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, []);

    if (redirect) {
        return <Redirect to={redirect}/>
    }

    return (
        <StandardContainer header={updateMode ? `Update Event "${formFields.name}"` : "Create a new Event"}>
            <Form.Row className="mb-3">

                <Col md={6}>
                    <Form.Label>Name</Form.Label>
                    <Form.Control
                        name="name"
                        onChange={onSimpleFormChange}
                        type="text"
                        value={formFields.name}
                        placeholder="Event Name"
                    />

                </Col>

                <Col md={6}>
                    <Form.Label>Location <em>(Where the event takes place)</em></Form.Label>
                    <Form.Control
                        name="location"
                        onChange={onSimpleFormChange}
                        type="text"
                        value={formFields.location}
                        placeholder="Location"
                    />
                </Col>
            </Form.Row>

            <Form.Row className="mb-3">
                <Col md={6}>
                    <Form.Label>From Date</Form.Label>
                    <CustomDatePicker
                        placeholder="From Date"
                        selected={formFields.from_date}
                        onChange={date => setFormFields({
                            ...formFields,
                            from_date: date,
                        })}
                    />
                </Col>

                <Col md={6}>
                    <Form.Label>To Date</Form.Label>
                    <CustomDatePicker
                        selected={formFields.to_date}
                        onChange={date => setFormFields({
                            ...formFields,
                            to_date: date,
                        })}
                    />
                </Col>

            </Form.Row>

            <Form.Group controlId="description">
                <Form.Label>Description</Form.Label>
                <Form.Control
                    name="description"
                    onChange={onSimpleFormChange}
                    as="textarea"
                    value={formFields.description}
                    placeholder="Product Description"
                    rows={3}
                />
            </Form.Group>

            <Row>
                <Col md={12} className="text-right">
                    <CustomButton
                        type="save"
                        onClick={() => {
                            axios.post(Endpoints.UPSERT_EVENT, formFields)
                                .then(response => {
                                    const {status, message} = get(response, 'data', {});
                                    if (status === 'success') {
                                        toast.success(message);
                                        setRedirect('/alph/events/');
                                    } else {
                                        toast.error(message);
                                    }
                                })
                        }}
                    >
                        <span>Save</span>
                    </CustomButton>
                </Col>
            </Row>

        </StandardContainer>
    );
}