import React from "react";
import AxiosTable from "../../../components/tables/AxiosTable";
import Endpoints from "../../../config/Endpoints";
import {useHistory} from 'react-router-dom';

export default function EventsTable() {

    const history = useHistory();

    return (
        <div>
            <div style={{maxWidth: '100%'}}>
                <AxiosTable
                    title={"All Events"}
                    endpoint={Endpoints.GET_EVENTS}
                    columns={[
                        {title: 'Name', field: 'name'},
                        {title: 'Description', field: 'description'},
                        {title: 'Location', field: 'location'},
                        {title: 'From', field: 'from_date'},
                        {title: 'To', field: 'to_date'}
                    ]}
                    actions={[
                        {
                            icon: 'add',
                            tooltip: 'Save Event',
                            isFreeAction: true,
                            onClick: (event, rowData) => history.push('/alph/events/upsert')
                        },
                        {
                            icon: 'edit',
                            tooltip: 'Edit Event',
                            onClick: (event, rowData) => history.push(`/alph/events/upsert/${rowData.id}`)
                        }
                    ]}
                />
            </div>
        </div>
    );
}