import React from 'react';
import {useSelector} from "react-redux";
import {Helmet} from "react-helmet";


/**
 * @param children
 * @param {string|null} header
 * @param {string|null} subHeader
 * @returns {JSX.Element}
 * @constructor
 */
export default function StandardContainer({
                                              children,
                                              header = null,
                                              subHeader = null
                                          }) {

    const {server, site} = useSelector(state => state.init);

    return (
        <div className="StandardContainer bg-white p-4">
            <div
                className="d-flex justify-content-between align-items-sm-center flex-column flex-sm-row mb-4 border-bottom pb-3">

                <Helmet>
                    <title>{site.company_name || 'TrenchDevs'} - {header}</title>
                </Helmet>

                <div className="mr-4 mb-3 mb-sm-0">
                    <h6 className="mb-0">{header}</h6>
                    <div className="small">
                        <span className="font-weight-500 text-primary"/> {subHeader}
                    </div>
                </div>

                <div className="mr-4 mb-3 mb-sm-0">
                    <h6 className="mb-0">Server Time (UTC)</h6>
                    <div className="small">
                        <span className="font-weight-500 text-primary"/> {server.time}
                    </div>
                </div>
            </div>
            {children}
        </div>
    );

}