import React from "react";
import {PlusCircle} from "react-feather";
import {Button} from "react-bootstrap";


export default function CustomButton({children, type = null, ...props}) {

    let icon = null;

    const iconProps = {
        size: 20,
        className:'pr-1',
    };

    switch (type) {
        case 'save':
        case 'plus':
            icon = <PlusCircle {...iconProps}/>
            break;
        default:
            break;
    }

    return (
        <Button variant="success" {...props}>
            {icon}
            {children}
        </Button>
    );
}